@import "global";
@import "header";
@import "text";
@import "menu";

#project_name {
  position: absolute;
  top: $margin-width;
  z-index: 100;
  transform:translate(-50%, -30%);
  left: 50%;
}


.carousel-item img {
  width: 100%;
  height: 100%;
  border-radius: 50px;
  object-fit: cover;
}

.carousel-control-prev, .carousel-control-next {
  opacity: 8;
  width: 10%;
}

section{
  padding-bottom: 1.5rem;
}


// SPLIT IN 2

#section-one {
position: relative;
width: 100%;
display: flex;
grid-gap: 0;
padding-top: 1rem;
}


#what-if {
  font-size: 60px;
  width: 100%;
  line-height: 1;
  transform:translate(-1%, 0);
}


#side-right, #side-left {position: relative; display: block}

#side-left {width: 75%;
margin-left: 1%;
  div {
padding-bottom: 1rem;
}
}

#side-right {
  width: 25%;
img {
  width: 80%;
  position: absolute;
}
}

/* VIDEO */
#video {
  width: 100%;
  margin: 3rem 0 3rem 0;
}

iframe {
  position: relative;
  margin: auto;
  display: block;
  width: 96VW;
  height: 54vw;
  overflow: hidden;
}


#img-bigger-container::-webkit-scrollbar,  {
  display: none;
}

#img-bigger-container {
  position: absolute; left: 0; right: 0; overflow: hidden;
  // height: $window-no-header;
  // border: solid;
}

#img-container {
  img {
    border-radius: 50px;
  }
padding: 0 $margin-width 0 $margin-width;
}

@media (min-width: 991.98px) {

#img2 { transform:translate(0, -30%) }
#img3 { transform:translate(0, -30%) }
#img4 { transform:translate(0, -50%) }
#img5 { transform:translate(0, -80%) }

}

#final-container {
  background-color: $grey;
}


#footer {
  padding: 0 $margin-width $margin-width $margin-width;

  img {
    width: $margin-width;
  }
}


// MOBILE

@media (max-width: 991.98px) {


  #project_name {
    position: relative;
    z-index: 0;
    transform:translate(-50%, -30%);
    left: 50%;
    margin: 0 0 1rem 0;
    padding: 0;
  }


  .carousel-item img {
    width: 100%;
    height: 100%;
    border-radius: 50px;
    object-fit: cover;
  }


  section{
    padding-bottom: 1.5rem;
  }


  // SPLIT IN 2

  #section-one {
  position: relative;
  width: 100%;
  display: block;
  }

  #side-left {width: 100%;
  margin-left: 5%;
    div {
  padding-bottom: 1rem;
  }
  }

  #side-right {
    width: 100%;
    text-align: center;
  img {
    width: 100%;
    position: relative;
  }
  }


  #what-if {
      font-size: 13vw;
      width: 100%;
      transform:translate(-4%, 0);
      // background-color: red;
  }

  /* VIDEO */
  .video {
    width: 100%;
  }

  iframe {
    position: relative;
    margin: auto;
    display: block;
    width: 100VW;
    height: 56.25vw;
    overflow: hidden;
  }

  #img-bigger-container {
    position: relative;
    height: auto;

#fake-container {
  display: none;
}

#img-container {
  .row {
    padding-bottom: 1rem;
  }
}

}

#footer {
  padding: 0 0 $margin-width-mobile 0;

  img {
    width: 2rem;
  }
}

#section-one {
  padding-top: 0;
}

#img-container {
  padding: 0}
}
